<div class="hello"><h1><router-link v-if="book &amp;&amp; uid &amp;&amp; book.length == 0" to="/friends">按此找朋友</router-link></h1><loader v-show="!users"></loader><div class="ui divider"></div><table class="ui celled striped collapsing table" v-if="book &amp;&amp; uid &amp;&amp; users"><thead><tr><th></th><th>有空時段</th></tr></thead><tbody><tr v-for="(h, index) in searchBy(users, mySearch)" :key="index" :class="{invisible: !book || !h || book.indexOf(h.uid) == -1}" v-show="book &amp;&amp; h &amp;&amp; book.indexOf(h.uid) &gt; -1"><td><router-link :to="'/flag/' + h.uid"><img class="ui avatar" :src="h.photoURL"><span>{{h.name}}</span></router-link></td><td><span>{{h.freetime}}</span></td></tr><!-- tr(v-for="(h, index) in searchBy(places, mySearch)",
:key="index",
  :class="{invisible: !book || !h || book.indexOf(h.uid) == -1}",
v-show="book && book.indexOf(h.uid) > -1"--><!--)td
  router-link(:to="'/flag/' + h.idx") {{h.name}}
td
  span {{h.freetime}}
--></tbody></table><!--.ui.four.doubling.cards.container(v-if="toList(places).length > 0 && toList(users).length > 0").ui.card(
